//vuex
import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import mutations from './mutations';
//
Vue.use(Vuex);
//store
const store = new Vuex.Store({
    //
    state:{
        //
        topics:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''},
          ],
          //
          unLoginmoreMenus:{
            login:''
          },
          //
          LoginmoreMenus:{
            create:'',
            center:'',
            msg:'',
            logout:''
          },
          isLogined:false,//
          loginname:null,//
          accesstoken:'',//accesstoken
          isShowMoreMenus:false,//
          author_id:''//id
    },
   
    //
    getters:{
      moreMenus:function(state){
        if(state.isLogined){
          return state.LoginmoreMenus
        }
        return state.unLoginmoreMenus
      },
      getTopicDescByTab:function(state){
        return function(tab) {
          for(var i=0;i<state.topics.length;i++){
            if(state.topics[i].name==tab){
              return state.topics[i].desc
            }
          }
        }
      }
    },
    mutations,
    actions
});
//
export default store;


